<template>
    <form class="card auth-card" @submit.prevent="submitHandler">
        <div class="card-content card-content-reg">
            <span class="card-title">Task manager</span>
            <div class="input-field">
                <input
                    id="email"
                    type="text"
                    v-model.trim="$v.email.$model"
                    :class="{invalid: ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
                >
                <label for="email">Email</label>
                <small class="helper-text invalid"
                    v-if="($v.email.$dirty && !$v.email.required)"
                >
                    Поле  Email не должно быть пустым
                </small>
                <small class="helper-text invalid"
                    v-else-if="($v.email.$dirty && !$v.email.email)"
                >
                    Введите корректный Email
                </small>
            </div>
            <div class="input-field">
                <input
                    id="password"
                    type="password"
                    v-model.trim="$v.password.$model"
                    :class="{invalid: ($v.password.$dirty && !$v.password.required) || ($v.password.$dirty && !$v.password.minLength)}"
                >
                <label for="password">Пароль</label>
                <small class="helper-text invalid"
                    v-if="($v.password.$dirty && !$v.password.required)"
                >
                    Введите пароль
                </small>
                <small class="helper-text invalid"
                    v-else-if="($v.password.$dirty && !$v.password.minLength)"
                >
                    Пароль должен содеражть {{$v.password.$params.minLength.min}} символов.
                </small>
            </div>
            <div class="input-field">
                <input
                    id="name"
                    type="text"
                    v-model.trim="$v.name.$model"
                    :class="{invalid: ($v.name.$dirty && !$v.name.required) || ($v.name.$dirty && !$v.name.minLength)}"
                >
                <label for="name">Имя</label>
                <small class="helper-text invalid"
                    v-if="($v.name.$dirty && !$v.name.minLength)"
                >
                    Минимальное количество символов: {{$v.name.$params.minLength.min}}. 
                </small>
            </div>
            <p>
                <label>
                    <input type="checkbox" v-model="agree"/>
                    <span>С правилами согласен</span>
                </label>
            </p>
        </div>
        <div class="card-action">
            <div>
                <button
                    class="btn waves-effect waves-light auth-submit"
                    type="submit"
                > 
                    Зарегистрироваться
                    <i class="material-icons right">send</i>
                </button>
            </div>

            <p class="center">
                Уже есть аккаунт?
                <router-link to="/login">Войти!</router-link>
            </p>
        </div>
    </form>

</template>
<script>
import {email, required, minLength} from 'vuelidate/lib/validators'
export default {
    name: 'registration',
    metaInfo: {
      title: 'Регистрация | Никита TM'
    },
    data: () => ({
        email: '',
        password: '',
        name:'',
        agree: false
    }),
    validations: {
        email: {email, required},
        password: {required, minLength: minLength(6)},
        name: {required, minLength: minLength(3)},
        agree: {checked: v => v}
    },
    methods: {
        async submitHandler(){
            if (this.$v.$invalid){
                this.$v.$touch()
                return
            }
            const formData = {
            email: this.email.toLowerCase(),
            password: this.password,
            name: this.name
            }
            try {
                await this.$store.dispatch('registration', formData)
                this.$router.push('/login?message=registration')
                
            } catch (error) {}
        }
    },
}
</script>